.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar > nav {
    margin:20px 20px 0px 20px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    flex-direction: row;
    padding: 0 10px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .navbar > nav > img {
    display: none;
    margin: 0px 3px 0px 0px;
  }

  .navbar > nav > img:hover {
    transition: 0.2s;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: inset 0px 0px 3px 0px rgba(0, 0, 0, 0.1);
  }

  .navbar > nav > ul {
    padding: 0%;
    display: flex;
    flex-direction: row;
    list-style: none;
  }
  
  .navbar > nav > ul > li:first-child {
    margin-left: 0px;
  }

    .navbar > nav > ul > li:last-child {
        margin-right: 0px;
    }

  .navbar > nav > ul > li {
    margin: 0px 3px;
    font-size: 14px;
    cursor: pointer;
    padding: 5px 10px;
    transition: 0.5s ease-in-out;
    font-weight: 400;
    letter-spacing: 1px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

  .navbar > nav > ul > :hover {
    transition: 0.2s;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: inset 0px 0px 3px 0px rgba(0, 0, 0, 0.1);
  }
  

  @media screen and (max-width: 420px){
    .navbar > nav > img {
      display: flex;
      position: relative;
      cursor: pointer;
    }
    .navbar > nav {
      height: 45px;
      width: 100%;
     flex-direction: column;
     justify-content: right;
     align-items: end;
     overflow-y: scroll;
     scroll-behavior: smooth;
    }

    .navbar {
      justify-content: right;
    }
    .navbar > nav > ul {
      margin: 10px;
      text-align: right;
      flex-direction: column;
    }
  }